    /// Get Agent Names
    getAgentListDetails() {
        this.agentDto.companyId = this.userDataDtoReturnSession.companyId;
        this.agentDto.column2 = "Working";
        this.agentDto.mode = "Sub";
        this.agentDto.status = "All";
        if ( this.isAgentAll == "AgentAll" ) {
            this.agentDto.mode = "Sub";
            this.agentDto.status = "All";
        } else {
            this.agentDto.mode = "mainBranch";
            if ( this.mainStationRpt == 'Paid Rpt Bkg Main Station' ) {
                this.agentDto.mainStation = this.userDataDtoReturnSession.mainStation;
            } else {
                this.agentDto.mainStation = this.userDataDtoReturnSession.mainAdminStation;
            }
        }

        this.getAgentList();
    }

    getAgentList = () => {
        this.masterReadService.getSourceListDetails( this.agentDto ).subscribe(
            ( response ) => {
                if ( response.length == 0 ) {
                    swal( {
                        title: "Warning",
                        text: "No records found!",
                        icon: "warning",
                        closeOnClickOutside: false,
                        closeOnEsc: false,
                    } );

                } else {

                    this.agentDetailsOptionsGet = response;
                    this.agentDtoAll.subStation = "ALL";
                    this.agentDetailsOptionsGet.push( this.agentDtoAll );

                }
            } ),
            ( error ) => console.log( error.json() ),
            () => console.log( 'done' );
    };

    ///get Consignor Index

    getConsignorIndexvalue() {
        for ( let i = 0; i < this.consignorIndexOptions.length; i++ ) {
            this.consignorIndexOptionsGet.push( { label: this.consignorIndexOptions[i], value: i } );
        }
    }
    ////Get Consignor Name 
    getConsignorInfo() {
        this.partyDto.companyId = this.userDataDtoReturnSession.companyId;
        //        this.indexVal = $( "#consignorIndex" ).val();
        //        this.indexVal = "Select Consignor Index";
        this.indexVal = "A";
        //        this.gstConsignor = $( "#consignorGstNumber" ).val();
        //        this.gstConsignor ="06AAACH8287K1Z5";

        this.partyDto.indexValue = this.indexVal;
        this.partyDto.gstNoConsignor = this.gstConsignor;

        if ( ( this.indexVal != null )
            && ( this.indexVal == "Select Consignor Index" ) ) {
            if ( this.gstConsignor != null ) {
                this.partyDto.mode = "LREntry";
                this.getConsignorDetailsList();
            }
        } else if ( ( this.indexVal != null )
            && ( this.indexVal == "ALL" ) ) {
            if ( this.gstConsignor != null ) {
                this.partyDto.mode = "LREntry";
            } else {
                this.partyDto.mode = "Index";
                this.partyDto.indexValue = "";
            }
            if ( this.partyDto.mode != null ) {
                this.getConsignorDetailsList();
            }
        } else {
            if ( this.gstConsignor != null ) {
                this.partyDto.mode = "IndexTinNo";
            } else {
                this.partyDto.mode = "Index";
            }
            if ( this.partyDto.mode != null ) {
                this.getConsignorDetailsList();
            }
        }
    }
    getConsignorDetailsList = () => {
        this.masterReadService.getConsignorDetails( this.partyDto ).subscribe(
            ( response ) => {
                if ( response.length == 0 ) {
                    swal( {
                        title: "Warning",
                        text: "No records found!",
                        icon: "warning",
                        closeOnClickOutside: false,
                        closeOnEsc: false,
                    } );
                } else {

                    this.partyDetailsOptionsGet = response;
                    //                    this.partyDtoAll.consignorName = "ALL";
                    //                    this.partyDetailsOptionsGet.push( this.partyDtoAll );
                    //                    console.log( this.partyDetailsOptionsGet );
                    this.srcLists = response;

                }
            } ),
            ( error ) => console.log( error.json() ),
            () => console.log( 'done' );
    };
